/* Contenedor tipo tarjeta */
.product-card {
  background: linear-gradient(135deg, #c7d2fd, #a0c4ff);
  border-radius: 15px;
  padding: 30px 40px;
  max-width: 70%;
  margin: 0 auto;
  box-shadow: 0 12px 25px rgba(0,0,0,0.12);
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Título llamativo */
.product-card-title {
  color: #003366;
  font-weight: 700;
  font-size: 1.8rem;
  margin-bottom: 25px;
  user-select: none;
  text-align: center;
  letter-spacing: 1.5px;
}

/* Formulario en línea con input y botón */
.product-card-form {
  width: 100%;
  display: flex;
  gap: 15px;
}

/* Input sin bordes fuertes, con sombra suave */
.product-card-input {
  flex-grow: 1;
  padding: 14px 18px;
  border-radius: 12px;
  border: none;
  box-shadow: 0 4px 12px rgba(0, 50, 120, 0.15);
  font-size: 1.1rem;
  font-weight: 500;
  outline-offset: 4px;
  transition: box-shadow 0.3s ease;
}

.product-card-input::placeholder {
  color: #7a8ba6;
  font-style: italic;
}

.product-card-input:focus {
  box-shadow: 0 4px 15px rgba(0, 80, 150, 0.6);
  background-color: #f0f7ff;
}

/* Botón con gradiente y sombra */
.product-card-button {
  background: #003366;
  color: white;
  font-weight: 700;
  font-size: 1.1rem;
  border: none;
  border-radius: 12px;
  padding: 14px 30px;
  cursor: pointer;
  box-shadow: 0 5px 15px rgba(0,80,150,0.7);
  transition: background 0.3s ease;
  user-select: none;
}

.product-card-button:hover {
  background: linear-gradient(45deg, #003366, #001f4d);
  box-shadow: 0 7px 20px rgba(0,50,100,0.85);
}
